<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data CPKB Pembelian</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="success mr-2" @click="dialogPengganti = true">
                            <v-icon size="18" class="mr-2">mdi-pencil</v-icon>
                            Tambah Pengganti
                        </v-btn>
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="warning mr-2" to="/formpengujianb" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Form Uji
                        </v-btn>
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="blue ml-2" to="/labelkarantina" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Label Karantina
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="dbelicpkbs" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item.id'>
                                <td>{{ index + 1 }}</td>
                                <td>{{ item["Tanggal"] }}</td>
                                <td>{{ item["No Bukti CPKB"] }}</td>
                                <td>{{ item["No Suplier CPKB"] }}</td>
                                <td>{{ item["Kode Barang CPKB"] }}</td>
                                <td>{{ item["Jumlah"] }}</td>
                                <td>{{ item["No LP"] }}</td>
                                <td>{{ item["No Bet"] }}</td>
                                <td>{{ item["No Uji"] }}</td>
                                <td>{{ item["No Faktur"] }}</td>
                                <td>{{ item["Status"] }}</td>
                                <td>
                                    <v-btn icon color="indigo" title="Edit" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn v-if="item['Status'] === 'Belum Diuji'" icon color="indigo" title="Input Pengujian" light @click="inputUji(item)">
                                        <v-icon>mdi-table-edit</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" title="hapus" light @click='deleteData(item)'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                    <v-btn v-if="item['Status'] === 'Belum Diuji' || item['Status'] === 'Tidak Lulus'" icon color="error"  title="Reject" la light @click="rejectHandler(item)">
                                        <v-icon>mdi-block-helper</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialogPengganti" persistent max-width="1200px">
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Data Pengganti</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-2">Nomor Bukti</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-autocomplete single-line outlined dense :items="hbelipembelians" item-text="items" item-value="items" class=" mr-4"
                                    label="No Bukti" v-model="header">
                                     <template slot="item" slot-scope="data">
                                        {{ data.item["No Bukti"] }}
                                    </template>
                                     <template slot="selection" slot-scope="data">
                                        {{ data.item["No Bukti"] }}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Tanggal</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="Tanggal" class="mr-4"
                                    v-model="header['Tanggal']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No Suplier</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="No Suplier" class="mr-4"
                                    v-model="header['No Suplier']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No Faktur</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="No Faktur" class="mr-4"
                                    v-model="header['No Faktur']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No LP</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="No LP" class="mr-4"
                                    v-model="header['No LP']" required>
                                </v-text-field>
                            </v-col>  
                            <v-col cols="2">
                                <h3 class="mt-2">No Bet</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="No Bet" class="mr-4"
                                    v-model="header['No Bet']" required>
                                </v-text-field>
                            </v-col>
                             <v-col cols="2" >
                                        <h3 class="mt-2">Barang</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="TblStok" item-text="Kode Barang" item-value="Kode Barang" 
                                label="Kode Barang" class="title mr-4" v-model="header['Kode Barang']">    
                                    <template slot="item" slot-scope="data">
                                        {{ data.item["Kode Barang"] }} - {{ data.item["Nama Barang"] }}
                                    </template>
                                </v-autocomplete>
                            </v-col> 
                            <v-col cols="2">
                                <h3 class="mt-2">Jumlah</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field type="number" dense single-line outlined label="Jumlah" class="mr-4"
                                    v-model="header['Jumlah']" required>
                                </v-text-field>
                            </v-col>                   
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                    <v-layout align-end justify-end>
                        <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                        <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded   @click="PostPengganti()">Save</v-btn>
                    </v-layout>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
                <v-card-title>
                    <span class="headline">Edit Data CPKB</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                                <v-text-field label="Tanggal*" v-model="form.tanggal" readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No Bukti CPKB*" v-model="form.noBukti" readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No Suplier CPKB*" v-model="form.noSuplier" readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="Kode Barang CPKB*" v-model="form.kodeBarang" readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field type="number" label="Jumlah" v-model="form.jumlah" readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No LP*" v-model="form.noLP" required></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No Bet*" v-model="form.noBet" required></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No Uji*" v-model="form.noUji" required></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="No Faktur*" v-model="form.noFaktur" required></v-text-field>
                            </v-col>                            
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                    <v-layout align-end justify-end>
                        <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                        <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded   @click="setForm()">Save</v-btn>
                    </v-layout>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialogUji" persistent fullscreen>
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Data Uji</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-2">Nama Produk</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nama Produk" class=" mr-4"
                                    v-model="formUji.namaProduk" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Kode Produk</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Jumlah Sampel" class=" mr-4"
                                    v-model="formUji.kodeProduk" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Jumlah</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined type="number" label="Jumlah Sampel"
                                    class=" mr-4" v-model="formUji.jumlahSampel" required readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Jumlah Reject</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined type="number" label="Jumlah Reject"
                                    class=" mr-4" v-model="formUji.jumlahReject" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No Bets</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="No Bets" class="mr-4"
                                    v-model="formUji.noBets" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Tanggal Pengujian</h3>
                            </v-col>
                            <v-col cols="3" style="display: flex">
                                <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="formUji.tanggalPengujian"
                                            label="Tanggal Pengujian" class=" mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="formUji.tanggalPengujian" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Tanggal Kadaluwarsa</h3>
                            </v-col>
                            <v-col cols="3" style="display: flex">
                                <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="formUji.tanggalKadaluwarsa"
                                            label="Tanggal Kadaluwarsa" class=" mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="formUji.tanggalKadaluwarsa" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu1 = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu1.save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No Uji</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nomor Uji" class=" mr-4"
                                    v-model="formUji.noUji" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Pelaksana</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Pelaksana" class=" mr-4"
                                    v-model="formUji.pelaksana" required>
                                </v-text-field>
                            </v-col>
                            <!-- <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Kesimpulan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-select dense single-line outlined :items="Keterangan" class=" mr-4" item-text="name"
                                    item-value="kode" label="Kesimpulan" v-model="formUji.kesimpulan" required>
                                </v-select>
                            </v-col> -->
                        </v-row>
                        <h2>Spesifikasi Uji</h2><br>
                        <hr> <br>
                        <v-row no-gutters v-if="StatusJenis === 'UB'">
                            <v-col cols="1">
                                <h3 class="mt-2">Visual</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="VisualB" class=" mr-4" label="Visual"
                                    v-model="formSpesifikasiB.visual" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1" v-if="formSpesifikasiB.visual === 'Cairan'">
                                <h3 class="mt-2">warna</h3>
                            </v-col>
                            <v-col cols="2" v-if="formSpesifikasiB.visual === 'Cairan'">
                                <v-autocomplete dense single-line outlined :items="CairanWarna" class=" mr-4"
                                    label="Warna" v-model="formSpesifikasiB.warna" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1" v-if="formSpesifikasiB.visual === 'Serbuk'">
                                <h3 class="mt-2">warna</h3>
                            </v-col>
                            <v-col cols="2" v-if="formSpesifikasiB.visual === 'Serbuk'">
                                <v-autocomplete dense single-line outlined :items="CairanSerbuk" class=" mr-4"
                                    label="Warna" v-model="formSpesifikasiB.warna" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Volume</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Volume" class=" mr-4"
                                    v-model="formSpesifikasiB.volume" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Berat Jenis</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Berat Jenis" class=" mr-4"
                                    v-model="formSpesifikasiB.beratJenis" placeholder="0,80 - 1,20" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">pH</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="pH" class=" mr-4"
                                    v-model="formSpesifikasiB.ph" placeholder="3,50 - 7,00" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                        <v-row no-gutters v-else>
                            <v-col cols="1">
                                <h3 class="mt-2">Visual</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="VisualP" class=" mr-4" label="Visual"
                                    v-model="formSpesifikasiP.visual" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Dimensi</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Dimensi" class=" mr-4"
                                    v-model="formSpesifikasiP.dimensi" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Warna</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="WarnaP" class=" mr-4" label="Warna"
                                    v-model="formSpesifikasiP.warna" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="3"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Volume</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-autocomplete dense single-line outlined :items="VolumeP" class=" mr-4" label="Volume"
                                    v-model="formSpesifikasiP.volume" required>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Berat</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Berat" class=" mr-4"
                                    v-model="formSpesifikasiP.berat" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                        <!-- Data reject -->
                        <h2 v-if="formUji.jumlahReject > 0">Spesifikasi Uji Reject</h2><br>
                        <hr v-if="formUji.jumlahReject > 0"> <br v-if="formUji.jumlahReject > 0">
                        <v-row no-gutters v-if="StatusJenis === 'UB' && formUji.jumlahReject > 0">
                            <v-col cols="1">
                                <h3 class="mt-2">Visual</h3>
                            </v-col>
                            <v-col cols="2">
                                 <v-text-field dense single-line outlined label="Visual" class=" mr-4"
                                    v-model="formSpesifikasiBR.visual" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">warna</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="warna" class=" mr-4"
                                    v-model="formSpesifikasiBR.warna" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Volume</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Volume" class=" mr-4"
                                    v-model="formSpesifikasiBR.volume" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Berat Jenis</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Berat Jenis" class=" mr-4"
                                    v-model="formSpesifikasiBR.beratJenis" placeholder="0,80 - 1,20" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">pH</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="pH" class=" mr-4"
                                    v-model="formSpesifikasiBR.ph" placeholder="3,50 - 7,00" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                        <v-row no-gutters v-else-if="StatusJenis === 'UP' && formUji.jumlahReject > 0">
                            <v-col cols="1">
                                <h3 class="mt-2">Visual</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Visual" class=" mr-4"
                                    v-model="formSpesifikasiPR.visual" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Dimensi</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="dimensi" class=" mr-4"
                                    v-model="formSpesifikasiPR.dimensi" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Warna</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="warna" class=" mr-4"
                                    v-model="formSpesifikasiPR.warna" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Volume</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="volume" class=" mr-4"
                                    v-model="formSpesifikasiPR.volume" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Berat</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Berat" class=" mr-4"
                                    v-model="formSpesifikasiPR.berat" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-layout align-end justify-end>
                        <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
