                this.datauji.append('pelaksana', this.formUji.pelaksana);
                this.datauji.append('kesimpulan', 'Lulus');
                var uri = this.$apiUrl + 'tabelujicpkbs'
                
                await this.$http.post(uri, this.datauji)

                // Get id
                var uri = this.$apiUrl + 'dbelicpkbs/maxid'
             
                var x = await this.$http.get(uri, this.dataSpesifikasiB)
                this.tampungID = x.data[0]
                    console.log(this.formUji.noUji)
                    // Pengecekan spesifikasi apakah Bahan Baku atau Bahan Pengemas || Masuk Data Induk Pasti
                    if ((this.formUji.noUji).substring(0, 2) === 'UB') //Jika Bahan Baku : maka
                    {
                        console.log("Masuk B")
                        // Input Data spesifikasi bahan baku
                        this.dataSpesifikasiB.append('id', this.tampungID['maksimum']);
                        this.dataSpesifikasiB.append('visual', this.formSpesifikasiB.visual);
                        this.dataSpesifikasiB.append('warna', this.formSpesifikasiB.warna);
                        this.dataSpesifikasiB.append('volume', this.formSpesifikasiB.volume);
                        this.dataSpesifikasiB.append('beratJenis', this.formSpesifikasiB.beratJenis);
                        this.dataSpesifikasiB.append('ph', this.formSpesifikasiB.ph);
                        var uri = this.$apiUrl + 'spesifikasiubcpkbs'
                        
                        await this.$http.post(uri, this.dataSpesifikasiB)
                    } else if ((this.formUji.noUji).substring(0, 2) === 'UP') { //jika bahan pengemas, maka :
                        console.log("Masuk P")
                        // Input spesifikasi bahan pengemas
                        this.dataSpesifikasiP.append('id', this.tampungID["maksimum"]);
                        this.dataSpesifikasiP.append('visual', this.formSpesifikasiP.visual);
                        this.dataSpesifikasiP.append('dimensi', this.formSpesifikasiP.dimensi);
                        this.dataSpesifikasiP.append('warna', this.formSpesifikasiP.warna);
                        this.dataSpesifikasiP.append('volume', this.formSpesifikasiP.volume);
                        this.dataSpesifikasiP.append('berat', this.formSpesifikasiP.berat);
                        var uri = this.$apiUrl + 'spesifikasiupcpkbs'
                        
                        await this.$http.post(uri, this.dataSpesifikasiP)
                    }
                    
                // Cek Reject
                if(this.formUji.jumlahReject > 0){
                    this.datauji = new FormData
                    this.datauji.append('namaProduk', this.formUji.namaProduk);
                    this.datauji.append('kodeProduk', this.formUji.kodeProduk);
                    this.datauji.append('jumlahSampel', this.formUji.jumlahReject);
                    this.datauji.append('noBets', this.formUji.noBets);
                    this.datauji.append('tanggalPengujian', this.formUji.tanggalPengujian);
                    this.datauji.append('tanggalKadaluwarsa', this.formUji.tanggalKadaluwarsa);
                    this.datauji.append('noUji', this.formUji.noUji);
                    this.datauji.append('pelaksana', this.formUji.pelaksana);
                    this.datauji.append('kesimpulan', 'Reject');
                    var uri = this.$apiUrl + 'tabelujicpkbs'
                  
                    await this.$http.post(uri, this.datauji)

                    var uri = this.$apiUrl + 'dbelicpkbs/maxid'
                   
                    var y =  await this.$http.get(uri, this.dataSpesifikasiB)
                        this.tampungID = y.data[0]
                        // Pengecekan spesifikasi apakah Bahan Baku atau Bahan Pengemas || Masuk Data Induk Pasti
                        if (this.formUji.noUji.substring(0, 2) === 'UB') //Jika Bahan Baku : maka
                        {
                            console.log("Masuk B")
                            // Input Data spesifikasi bahan baku
                            this.dataSpesifikasiB.append('id', this.tampungID['maksimum']);
                            this.dataSpesifikasiB.append('visual', this.formSpesifikasiBR.visual);
                            this.dataSpesifikasiB.append('warna', this.formSpesifikasiBR.warna);
                            this.dataSpesifikasiB.append('volume', this.formSpesifikasiBR.volume);
                            this.dataSpesifikasiB.append('beratJenis', this.formSpesifikasiBR.beratJenis);
                            this.dataSpesifikasiB.append('ph', this.formSpesifikasiBR.ph);
                            var uri = this.$apiUrl + 'spesifikasiubcpkbs'
                           
                            await this.$http.post(uri, this.dataSpesifikasiB)
                        } else if (this.formUji.noUji.substring(0, 2) === 'UP') { //jika bahan pengemas, maka :
                            console.log("Masuk P")
                            // Input spesifikasi bahan pengemas
                            this.dataSpesifikasiP.append('id', this.tampungID["maksimum"]);
                            this.dataSpesifikasiP.append('visual', this.formSpesifikasiPR.visual);
                            this.dataSpesifikasiP.append('dimensi', this.formSpesifikasiPR.dimensi);
                            this.dataSpesifikasiP.append('warna', this.formSpesifikasiPR.warna);
                            this.dataSpesifikasiP.append('volume', this.formSpesifikasiPR.volume);
                            this.dataSpesifikasiP.append('berat', this.formSpesifikasiPR.berat);
                            var uri = this.$apiUrl + 'spesifikasiupcpkbs'
          
                            await this.$http.post(uri, this.dataSpesifikasiP)
                        }
                }

                // Status Entry CPKB
                this.datauji.append('status', 'Lulus ' + ((this.formUji.jumlahSampel) - (this.formUji.jumlahReject)) + ' Reject ' + (this.formUji.jumlahReject));
                var uri = this.$apiUrl + 'dbelicpkbs/' + this.formUji.noUji 
                await this.$http.post(uri, this.datauji)

                this.dialogUji = false
                this.snackbar = true; //mengaktifkan snackbar
                this.color = 'green'; //memberi warna snackbar
                this.text = 'Berhasil';
                this.getData()

               
            },
            updateData() {
                this.dbelicpkb.append('tanggal', this.form.tanggal);
                this.dbelicpkb.append('noSuplier', this.form.noSuplier);
                this.dbelicpkb.append('noBukti', this.form.noBukti);
                this.dbelicpkb.append('kodeBarang', this.form.kodeBarang);
                this.dbelicpkb.append('jumlah', this.form.jumlah);
                this.dbelicpkb.append('noLP', this.form.noLP);
                this.dbelicpkb.append('noBet', this.form.noBet);
                this.dbelicpkb.append('noUji', this.form.noUji);
                this.dbelicpkb.append('noFaktur', this.form.noFaktur);
                var uri = this.$apiUrl + 'dbelicpkbs/update/' + this.form.noUji
                this.load = true
                this.$http.post(uri, this.dbelicpkb).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.getData(); //mengambil data user
                    this.resetForm();
                    this.Activity('Melakukan Update CPKB Pembelian');
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })
            },
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.dialog = true;
                this.form= {
                    noSuplier: item["No Suplier CPKB"],
                    noBukti: item["No Bukti CPKB"],
                    jumlah: item["Jumlah"],
                    kodeBarang: item["Kode Barang CPKB"],
                    noLP: item["No LP"],
                    noBet: item["No Bet"],
                    noFaktur: item["No Faktur"],
                    noUji: item["No Uji"],
                    tanggal: item["Tanggal"],
                }
            },

    

            inputUji(item){
                var uri = this.$apiUrl + 'tblstoks/' + item["Kode Barang CPKB"]
                this.$http.get(uri).then(response => {
                this.datacpkbNama = response.data["Nama Barang"]
                this.dialogUji = true;
                this.formUji= {
                    namaProduk: this.datacpkbNama,
                    kodeProduk: item["Kode Barang CPKB"],
                    jumlahSampel: item["Jumlah"],
                    noBets: item["No Bet"],
                    noUji: item["No Uji"],
                }
                this.StatusJenis = (this.formUji.noUji).substring(0,2)
                })
            },
            deleteData(item) { //mengahapus data
                var uri = this.$apiUrl + 'dbelicpkbs/' + item["No Uji"] 
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    this.Activity('Melakukan Delete data CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })

                // Delete data Uji CPKB
                var uri = this.$apiUrl + 'tabelujicpkbs/' + item["No Uji"]
                this.$http.delete(uri)
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            setFormUji(){
                if (this.typeInput === 'new') {
                    this.sendDataUji()
                    
                } else {
                    this.updateDataUji()
                }
            },
            close() {
                this.dialog = false
                this.dialogUji = false
                this.dialogPengganti = false
                this.resetForm()
            },
            resetForm() {
                this.form= {
                    noSuplier: '',
                    noBukti: '',
                    jumlah: '',
                    kodeBarang: '',
                    noLP: '',
                    noBet: '',
                    noFaktur: '',
                    noUji: '',
                }
                this.formUji= {
                    namaProduk: '',
                    kodeProduk: '',
                    jumlahSampel: 0,
                    noBets: '',
                    tanggalPengujian: '',
                    pelaksana: '',
                    kesimpulan: '',
                }
                this.statusUpdate = true
                this.datacpkbNama= ''
            }
        },
        mounted() {
            this.getData();
            this.getDataHbeli();
            this.getDataTblStok();
        },
    }
</script>